




import { Component, Vue } from "vue-property-decorator";
import Axios from "axios";


@Component
export default class UploadFile extends Vue {
  public files = [];
  public errorMessage = null
  public origin =location.origin
  public async uploadFiles(event: any) {
    try {
      if (event.target.files.length == 0) return;
      let formData = new FormData();
      event.target.files.forEach((e: any) => {
        // console.log(e)
        formData.append("files", e);
      });
    let response =   await Axios.post(this.origin+'/api/uploadFile', formData);
    this.fetchUploadedFiles();
    } catch (error) {
       this.errorMessage = error.response.data.message
      // console.log(error.response);
    }
  }
  public async fetchUploadedFiles() {
    try {
      let response = await Axios.get(
       this.origin+'/api/fetchUploadedFiles'
      );
      if (response.data.uploadedFiles) this.files = response.data.uploadedFiles;
    // this.fetchUploadedFiles()
    } catch (error) {
      console.log(error);
    }
  }

  async mounted() {
    this.fetchUploadedFiles();
  }
}
